<template>
    <div class="zhucekehu">
      <mt-header title="注册客户" id="header">
        <router-link to="/" slot="left" >
          <mt-button icon="back"></mt-button>
        </router-link>
      </mt-header>
      <div class="con">
        <mt-field label="客户姓名" placeholder="请输入客户名" v-model="userName"></mt-field>
        <mt-field label="手机号" placeholder="请输入密码" type="password" ></mt-field>
        <mt-field label="身份证号" placeholder="请输入身份证号" type="tel" ></mt-field>
        <mt-field label="客户类型" placeholder="钻石会员" ></mt-field>

        <mt-field label="性别" placeholder=""></mt-field>
        <mt-field label="E-mail" placeholder=""></mt-field>
        <mt-field label="联系地址" placeholder=""></mt-field>

        <input type="checkbox" >我已经同意以下条款 <a href="#">用户使用协议</a>
        <mt-button type="primary" class="btn" @click="login">申请注册</mt-button>
      </div>
    </div>
</template>

<script>
  import axios from 'axios'
    export default {
        name: "zhucekehu",
      data(){
          return{
            userName:''
          }
      },
      methods:{
        login(){
          let that = this
          axios.post('/api/userlist/add', {
            name: this.userName
          },{withCredentials: true})
            .then(function (response) {
              console.log(response);

              if(response.data.ret){
                that.$router.push('/mydidi')
                window.alert("注册成功");
              }
            })
            .catch(function (error) {
              console.log(error);
            });
        }
      }
    }
</script>

<style scoped>
.btn{
  display: block;
  width: 90%;
  margin: 20px auto;
}
</style>



